import React from "react";
import s from "./Header.module.css";
import {NavLink} from "react-router-dom";
import {HeaderSearchReduxForm} from "./HeaderSearchForm/HeaderSearchForm";



const Header = (props) => {

    const newSearch=(values)=>{alert(values.headerSearch)}
        return (
        <div className={s.header}>
            <div className={s.headerLogo}>
                <img src="https://iqonic.design/themes/socialv/html/images/loader.png" alt='Ava'/>
            </div>
            <div className={s.headerSearch}>
                <HeaderSearchReduxForm onSubmit={newSearch}/>
            </div>
            <div className={s.headerNav}>
                <div className={s.navIcons1}>
                    <a href="#">
                        <img src='https://iqonic.design/themes/socialv/html/images/user/1.jpg'/>
                    </a>
                </div>
                <div className={s.navIcons}>
                    <a href="#">
                        <h6>Bni Cyst</h6>
                    </a>
                </div>
                <div className={s.navIcons}>
                    <a href="#">
                        <img src='http://s1.iconbird.com/ico/2013/3/637/w128h128139396832128.png'/>
                    </a>
                </div>
                <div className={s.navIcons}>
                    <a href="#">
                        <img src='https://cdn.icon-icons.com/icons2/259/PNG/128/ic_group_128_28325.png'/>
                    </a>
                </div>


            </div>
            <div>
                {props.isAuth
                    ? <div  className={s.navIcons_login}>
                        {props.login} <button className={s.headerSearch} onClick={props.logout}>Log out</button></div>
                    : <NavLink to='/login'>Login</NavLink>
                }
            </div>
        </div>
    )
};
export default Header;