import React from 'react';
import {reduxForm} from "redux-form";
import {Input} from "../common/formsControls/formsControls";
import { required} from "../../utils/validations/validator";
import {connect} from "react-redux";
import {login} from "../../Redax/authReducer";
import {Redirect} from "react-router-dom";
import s from "./../Header/Header.module.css";
import {createField} from "../common/createField/createField";


const LoginForm = ({handleSubmit}, {error}) => {
    return (
        <form onSubmit={handleSubmit}>
            {createField("Email", "email", [required], Input)}
            {createField("Password", "password", [required], Input, {type: "password"})}
            {createField(null, "rememberMe", [], Input, {type: "checkbox"}, "Remember me")}
            <div>
                <button className={s.headerLogin}>Login</button>
            </div>
            {error && <div className={s.formSummerError}>
                {error}
            </div>}
        </form>
    )
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm);

const Login = (props) => {
    const onSubmit = (FormData) => {
        props.login(FormData.email, FormData.password, FormData.rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to='/profile'/>
    }

    return <div>
        <h1>Login</h1><LoginReduxForm onSubmit={onSubmit}/>
    </div>
}

const mapStateToProps = (state) => {
    return {isAuth: state.auth.isAuth}
}

export default connect(mapStateToProps, {login})(Login);