import React from 'react';
import {addPostactionCreatore} from "../../../Redax/profile-reducer";
import CreatePost from "./CreatePost";
import {connect} from "react-redux";


const mapStateToProps=(state)=>{
    return {
        postsName:state.postPage.Postsname,
    }
}


const CreatePostContainer=connect(mapStateToProps,{ addPostactionCreatore})(CreatePost);

export default CreatePostContainer;