import React from "react";
import {Field, reduxForm} from "redux-form";
import s from "./../Header.module.css";
import {Input} from "../../common/formsControls/formsControls";
import {maxLengthCreator, required} from "../../../utils/validations/validator";

const maxLength100 = maxLengthCreator(100)

const HeaderSearchForm = (props) => {

    return (
        <form onSubmit={props.handleSubmit} className={s.searchbox}>
            <Field component={Input} name={"headerSearch"} className={s.search_input}
                   placeholder={"Type here to search..."}
                   validate={[required, maxLength100]}

            />
            <button className={s.searchButton}>Search</button>
        </form>
    )
}

export const HeaderSearchReduxForm = reduxForm({form:"headerSearch"})(HeaderSearchForm)

