const dialogsReducer = (state = initial, action) => {
    switch (action.type) {
        case ADD_SEND:
            let body = action.newMessageBody;
            return {
                ...state,
                messagess: [...state.messagess, {
                    id: 9,
                    message: body,
                    src: 'https://iqonic.design/themes/socialv/html/images/user/1.jpg',
                    time: '7:23'
                }]
            };
        default:
            return state;
    }
}


export const addSendactionCreator = (newMessageBody) => ({type: ADD_SEND, newMessageBody});

export default dialogsReducer;