const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 4,
                src1: 'https://sun9-44.userapi.com/impf/Bi8OhwW7p6F4oNQl89T4-vnDPLkERlRuZqBHYg/ehMAqGyCAdo.jpg?size=1280x854&quality=96&sign=8fbe2dc6d42955d068559129d0ba9d8f',
                name: action.newPost,
                name1: 'SaitBot',
                name2: '10 hour ago',
                src2: 'https://avatars.mds.yandex.net/get-zen_doc/237236/pub_5d1b95137782bf00adbe3694_5d1b9522f221ef00adfa605d/scale_1200',
                p3: '',
                src3: 'https://iqonic.design/themes/socialv/html/images/page-img/p1.jpg',
                src4: 'https://iqonic.design/themes/socialv/html/images/icon/01.png',
                span1: '0 Likes',
                time1: '0 Comment',
                src5: 'https://cdn.icon-icons.com/icons2/2036/PNG/128/sharing_share_icon_124236.png',
                text1: '0 Share',
                src6: 'https://iqonic.design/themes/socialv/html/images/user/02.jpg',
                text2: 'Monty Carlo',
                text3: 'Loren ipsum dolor sit amet',
                span2: '0 min',
                src7: 'https://iqonic.design/themes/socialv/html/images/user/03.jpg',
                text4: 'Paul Molive',
                text5: 'Loren ipsum dolor sit amet',
                span3: '0 min',
            };
            return {
                ...state,
                Postsname: [...state.Postsname, newPost],
            };
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_STATUS:
            return {
                ...state,
                status: action.status,
            };
        case DELETE_POST:

            return {
                ...state,
                Postsname: state.Postsname.filter(p => p.id != action.postId)
            };

        default:
            return state;
    }
}


export const addPostactionCreatore = (newPost) => ({type: ADD_POST, newPost});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
export const setStatus = (status) => ({type: SET_STATUS, status});
export const deletePost = (postId) => ({type: DELETE_POST, postId});

export const getStatus = (userId) => {
    return async (dispatch) => {
        let response = await profileAPI.getStatus(userId);
        dispatch(setStatus(response.data));

    }
}


export const updateStatus = (status) => {
    return async (dispatch) => {
        let response = await profileAPI.updateStatus(status);
        if (response.data.resultCode === 0) {
            dispatch(setStatus(status));
        }
    }
}


export const getUsersProfileCreator = (userId) => {
    return async (dispatch) => {
        let response = await usersAPI.getProfile(userId);
        dispatch(setUserProfile(response.data));
    }
}

export default profileReducer;