import React from 'react';
import Preloader from "../../common/Preloader/Preloader";
import s from "../Content/Content.module.css";
import ProfileStatusWithHooks from "./ProfileStatusWithHook";


const UserProfile = (props) => {

if (!props.profile){
    return <Preloader/>
}
    return (
        < div className={s.content}>
            <div className={s.banner} >
                <div>
                    <img src="https://iqonic.design/themes/socialv/html/images/page-img/profile-bg1.jpg" alt="" />
                </div>
                <div className={s.profile_img}>
                    <img src={props.profile.photos.large}/>
                    < ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
                    <p>{props.profile.fullName}</p>
                    <p>{props.profile.contacts.website}</p>
                </div>
            </div>
            <div className={s.social_icons}>
                <div>
                    <a href='#' >{!props.profile.contacts.website ?props.profile.contacts.facebook:null}</a>
                </div>
                <div>
                    <a href='#' ><img src='https://iqonic.design/themes/socialv/html/images/icon/09.png' alt="twiter"/></a>
                </div>
                <div>
                    <a href='#' ><img src='https://iqonic.design/themes/socialv/html/images/icon/10.png' alt="inst"/></a>
                </div>
                <div>
                    <a href='#' ><img src='https://iqonic.design/themes/socialv/html/images/icon/11.png' alt="icon"/></a>
                </div>
                <div>
                    <a href='#' ><img src='https://iqonic.design/themes/socialv/html/images/icon/12.png' alt='icon'/></a>
                </div>
                <div>
                    <a href='#' ><img src='https://iqonic.design/themes/socialv/html/images/icon/13.png' alt="icon"/></a>
                </div>
            </div>
        </div>

    )
}
export default UserProfile;

