import React from 'react';
import {connect} from "react-redux";
import {getStatus, getUsersProfileCreator, setUserProfile, updateStatus} from "../../Redax/profile-reducer";
import Profile from "./Profile";
import {withRouter} from "react-router-dom";
import {withAuthRedirect} from "../hoc/withAuthRedirect";
import {compose} from "redux";


class ProfileContainer extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.authorisedUserId;
            if (!userId) {
                userId = this.props.history.push('/login');
            }
        }
        //      JSX <Redirect to=''/>
        //    redirect,  props  history.push (  path to)
        //       (      UI),    ,
        //    render()/JSX     state     

        this.props.getUsersProfileCreator(userId);
        this.props.getStatus(userId);
    }

    render() {
        return (
            <Profile {...this.props} profile={this.props.profile}/>
        )
    }
}


let mapStateToProps = (state) => {
    return ({
        profile: state.postPage.profile,
        status: state.postPage.status,
        isAuth: state.auth.isAuth,
        authorisedUserId: state.auth.userId,
    })
};

export default compose(
    withAuthRedirect,
    withRouter,
    connect(mapStateToProps, {setUserProfile, getUsersProfileCreator, getStatus, updateStatus}),
)(ProfileContainer);
